@charset "UTF-8";
@font-face {
  font-family: "LabGrotesque";
  src: url("../fonts/LabGrotesque-Regular.woff2") format("woff2"), url("../fonts/LabGrotesque-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "LabGrotesque-Bold";
  src: url("../fonts/LabGrotesque-Bold.woff2") format("woff2"), url("../fonts/LabGrotesque-Bold.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  height: 100%;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

button {
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer; }
  button:hover, button:focus, button:active {
    border: none;
    outline: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button-text {
  color: #ea0029; }
  .button-text:hover, .button-text:focus {
    color: #f53a31; }
  .button-text:active {
    color: #ea0029; }

.button-big {
  background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
  border-radius: 6px; }
  .button-big:hover, .button-big:focus, .button-big:active {
    background-color: #ea0029; }

.calc {
  width: 100%;
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  border: none; }

.calc__text {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif; }

.calc__item {
  width: 100%;
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid #dfe3e6; }

.calc__label {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  width: 100%;
  color: #808080;
  padding-left: 30px;
  cursor: pointer; }

.calc__checkbox {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 17px;
  left: 0;
  border: 1px solid #dfe3e6;
  border-radius: 6px; }
  .calc__checkbox:hover {
    border-color: #000000; }

.calc__input:checked + .calc__checkbox::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: -1px;
  left: -1px;
  background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
  background-image: url("../img/icons/check.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
  border-radius: 6px; }

.calc__input:focus + .calc__checkbox,
.calc__input:focus + .calc__checkbox::before {
  border: 1px solid #000000; }

.calc__input:disabled + .calc__checkbox::before {
  border-color: #bec5cc; }

.calc__input:checked:disabled + .calc__checkbox::before {
  border-color: #bec5cc; }

.calc__sum {
  color: #000000; }

.choice {
  margin-bottom: 20px; }

.choice__text {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  margin-bottom: 24px; }

.choice__list {
  display: flex; }

.choice__item {
  margin-right: 8px; }

.choice__button {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  background: #eef0f2;
  padding: 6px 12px;
  border-radius: 50px; }
  .choice__button:hover, .choice__button:focus {
    background: #dfe3e6; }
  .choice__button--active {
    color: #ffffff;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56; }
    .choice__button--active:hover, .choice__button--active:focus {
      background: #ea0029; }

@media (min-width: 768px) {
  .choice {
    display: flex;
    margin-bottom: 40px; }
  .choice__text {
    margin-right: 76px; } }

.main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
  overflow-y: auto; }

.main__button {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  padding: 12px 24px;
  color: #ffffff;
  background: none;
  border: 1px solid #ffffff; }
  .main__button:hover, .main__button:focus, .main__button:active {
    border: 1px solid #ffffff; }

@media (min-width: 768px) {
  .main__button {
    margin: 0;
    padding: 0;
    font-family: "LabGrotesque", "Аrial", sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
    padding: 16px 32px; }
  .main--active {
    padding-bottom: 40px;
    align-items: start;
    background: rgba(0, 0, 0, 0.3); } }

.popup {
  position: relative;
  min-height: 100vh;
  max-height: 800px;
  padding: 32px 16px 16px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: start; }

.popup__title {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  margin-bottom: 16px; }

.popup__text {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 24px;
  color: #808080; }

.popup__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  flex-grow: 1; }

.popup__calc {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  margin-bottom: 24px; }

.popup__button {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  width: 100%;
  margin-top: auto;
  padding: 12px;
  color: #ffffff;
  box-shadow: 0px 0px 24px rgba(234, 0, 41, 0.33); }

.popup__close {
  width: 12px;
  height: 12px;
  position: absolute;
  top: 22px;
  right: 22px; }
  .popup__close:focus {
    outline: 1px solid #000000;
    outline-offset: 3px; }

@media (min-width: 768px) {
  .popup {
    min-height: auto;
    width: 453px;
    top: 120px;
    margin-bottom: 40px;
    border-radius: 30px; }
  .popup__title {
    margin: 0;
    padding: 0;
    font-family: "LabGrotesque", "Аrial", sans-serif;
    font-size: 28px;
    line-height: 40px;
    font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
    margin-bottom: 16px; }
  .popup__text {
    margin: 0;
    padding: 0;
    font-family: "LabGrotesque", "Аrial", sans-serif;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 24px; }
  .popup__button {
    margin: 0;
    padding: 0;
    font-family: "LabGrotesque", "Аrial", sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
    margin-top: auto;
    padding: 16px; } }

@media (min-width: 1440px) {
  .popup {
    width: 552px; } }

.wage {
  width: 100%; }

.wage__label {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-family: "LabGrotesque-Bold", "Аrial", sans-serif;
  margin-bottom: 8px; }

.wage__input {
  margin: 0;
  padding: 0;
  font-family: "LabGrotesque", "Аrial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  width: 100%;
  padding: 8px 10px;
  margin-bottom: 8px;
  border: 1px solid #dfe3e6;
  border-radius: 3px;
  outline: none; }
  .wage__input:hover {
    border-color: #000000; }
  .wage__input:focus, .wage__input:active {
    border-color: #dfe3e6; }
  .wage__input:disabled {
    border-color: #808080; }
  .wage__input:invalid:not(:placeholder-shown) {
    border-color: #ea0029; }
